@import url('https://fonts.googleapis.com/css2?family=Arya&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Pacifico&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Quicksand&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');

.blog {
    display: flex;
    flex-direction: column;
    max-width: 2550px;
}

.blogHeader {
    display: flex;
    flex-direction: row;
    height: 300px;
    background-image: url("../../images/home_bg2.png");
    background-position: left 10% top 10%;
    background-size: 35%;
    background-repeat: no-repeat;
}

.leftBlogContainer {
    display: flex;
    flex-direction: column;
    width: 50%;
    align-self: center;
}

.leftBlogContentContainer {
    width: 60%;
    align-self: flex-end;
    margin-right: 30px;
    justify-content: center;
    align-items: center;
}

.blogDesc {
    font-family: 'Times New Roman', Times, serif;
    font-size: 18px;
}

.rightBlogContainer {
    display: flex;
    width: 50%;
    align-self: center;
}

.blogImg {
    width: 30%;
    margin-left: 150px;
}

.blogDivider {
    width: 100%;
    height: 2px;
    background-color: gray;
}

.card {
    background-color: rgb(242, 242, 242);
    display: flex;
    flex-direction: row;
    width: 700px;
    height: 150px;
    padding: 10px;
    margin: 30px 200px 0px 200px;
    box-shadow: 0px 0px 0px grey;
    -webkit-transition:  box-shadow .6s ease-out;
    box-shadow: .8px .9px 3px grey;
    cursor: pointer;
}

.card:hover {
    box-shadow: 1px 8px 20px grey;
    -webkit-transition:  box-shadow .6s ease-in;
}

.cardImg {
    object-fit: cover;
    width: 230px;
    padding: 5px;
}

.cardInfo {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
    margin-left: 5px;
    padding: 5px;
}

.cardTag {
    border-radius: 10px;
    padding: 0px 3px;
}

.cardTopic {
    font-family: 'Abril Fatface', cursive;    
    font-size: 23px;
    line-height: 22px;
    text-align: left;
    margin-bottom: 5px;
}

.cardDesc {
    text-align: left;
    color: rgb(74, 74, 74);
}


@media screen and (max-width: 1280px) {
    .leftBlogContainer {
        display: flex;
        flex-direction: column;
        width: 80%;
        align-self: center;
    }

    .blogImg {
        width: 50%;
        margin-left: 50px;
    }
    
    .card {
        background-color: rgb(242, 242, 242);
        display: flex;
        flex-direction: row;
        width: 700px;
        height: 150px;
        padding: 10px;
        margin: 30px 200px 0px 80px;
        box-shadow: 0px 0px 0px grey;
        -webkit-transition:  box-shadow .6s ease-out;
        box-shadow: .8px .9px 3px grey;
        cursor: pointer;
    }
}
